.ico-mini, .com-checkbox .emulate, .com-radio .emulate {
  background-image: url(../image/ico-mini.png);
  display: inline-block;
  vertical-align: middle;
  background-size: 64px 43px; }

.ico-mini-checkbox-checked, .com-checkbox .native:checked + .emulate {
  background-position: -25px 0px;
  width: 18px;
  height: 18px; }

.ico-mini-checkbox, .com-checkbox .emulate {
  background-position: 0px -25px;
  width: 18px;
  height: 18px; }

.ico-mini-date {
  background-position: 0px 0px;
  width: 20px;
  height: 20px; }

.ico-mini-radio-checked, .com-radio .native:checked + .emulate {
  background-position: -23px -25px;
  width: 16px;
  height: 16px; }

.ico-mini-radio, .com-radio .emulate {
  background-position: -48px 0px;
  width: 16px;
  height: 16px; }

@keyframes scale-in-ease {
  0% {
    opacity: 0;
    transform: scale(0); }
  50% {
    transform: scale(1.1); }
  100% {
    opacity: 1;
    transform: scale(1) perspective(1200px); } }

@keyframes scale-out {
  from {
    opacity: 1;
    transform: scale(1); }
  to {
    opacity: 0;
    transform: scale(0.8); } }

@keyframes rotate-circles {
  0% {
    transform: rotate(0); }
  100% {
    transform: rotate(360deg); } }

.color-white {
