  color: #313131; }

.color-silver {
  color: #CCCCCC; }

.com-btn {
  display: inline-block;
  text-align: center;
  cursor: pointer;
  touch-action: manipulation;
  vertical-align: middle;
  overflow: hidden;
  white-space: nowrap;
  user-select: none;
  background: #DF0338;
  height: 40px;
  line-height: 40px;
  padding: 0 20px;
  color: #fff;
  border-radius: 0;
  transition: all .5s ease;
  border: 0; }
  .com-btn:hover, .com-btn:focus, .com-btn.focus {
    text-decoration: none;
    background: #d00334; }
  .com-btn[disabled], .com-btn.disabled {
    cursor: not-allowed;
    pointer-events: none;
    filter: grayscale(1);
    opacity: .65; }
  .com-btn.is-carmine {
    background-color: #A10417;
    border: 1px solid #C30020; }
    .com-btn.is-carmine:hover {
      background-color: #880313; }
  .com-btn.is-mine-shaft {
    background-color: #333333;
    border: 0; }
    .com-btn.is-mine-shaft:hover {
      background-color: #262626; }

.com-input {
  display: inline-block;
  vertical-align: middle;
  border-radius: 0;
  padding: 0 10px;
  height: 40px;
  line-height: 40px;
  color: #fff;
  background: transparent;
  border: 1px solid #313131; }
  .com-input:hover, .com-input:focus, .com-input.focus {
    box-shadow: 0 0 10px 1px rgba(255, 255, 255, 0.1); }
  .com-input.placeholder {
    color: #b2b2b2; }
  .com-input[readonly], .com-input[disabled] {
    pointer-events: none;
    filter: grayscale(1);
    opacity: .65; }

.com-select {
  display: inline-block;
  vertical-align: middle;
  background: #fff;
  border: 0;
  border-radius: 0;
  padding: 0 10px;
  height: 40px;
  line-height: 40px;
  color: #1a1a1a; }
  .com-select:hover, .com-select:focus, .com-select.focus {
    box-shadow: 0 0 10px 1px rgba(255, 255, 255, 0.1); }

.com-nail {
  text-align: center;
  display: block; }
  .com-nail:before {
    content: "";
    height: 100%;
    vertical-align: middle;
    display: inline-block;
    margin-left: -5px; }
  .com-nail img {
    vertical-align: middle;
    max-height: 100%; }

.com-checkbox {
  display: inline-block;
  cursor: pointer;
  vertical-align: middle; }
  .no-canvas .com-checkbox .native {
    display: inline-block;
    vertical-align: middle;
    margin-top: -2px; }
  .no-canvas .com-checkbox .emulate {
    display: none; }
  .com-checkbox .native {
    display: none; }
  .com-checkbox .emulate {
    margin: -2px 8px 0 0; }

.com-radio {
  display: inline-block;
  cursor: pointer;
  vertical-align: middle; }
  .no-canvas .com-radio .native {
    display: inline-block;
    vertical-align: middle;
    margin-top: -2px; }
  .no-canvas .com-radio .emulate {
    display: none; }
  .com-radio .native {
    display: none; }
  .com-radio .emulate {
    margin: -2px 8px 0 0; }

.wrapper {
  width: 1200px;
  margin: 0 auto;
  position: relative; }

.page.is-debug {
  position: relative; }
  .page.is-debug:before {
    content: "";
    position: absolute;
    width: 100%;
